import { getAllCity } from '../../../api/city.js';
export default {
	data() {
		return {
			address: {
			},
			citys: []
		};
	},
	onShow() {
		let map = new Map();
		let dest = []
		getAllCity().then(
			res => {
				console.log('res'+JSON.stringify(res))
				if (res.errno === 0) {
					this.citys = res.data;
					console.log('city' + JSON.stringify(this.citys));
					this.citys.forEach(item=>{
						console.log('dest'+JSON.stringify(dest))
						if(!map[item.upper]){
							console.log('111')
							dest.push(item)
							map[item.upper] = dest
							dest= []
						} else{
							map[item.upper].push(item)
						}
					})
					console.log('map'+JSON.stringify(map))
					this.address = map
				}
			},
			err => {
				console.log('err' + JSON.stringify(err));
			}
		);
	}
};
